import React from 'react'
import { Box, Text, Flex, Relative, Absolute, Image } from 'rebass'
import Description from './Description'
import Number from './Number'
import Title from './Title'
import Underline from './Underline'
import UnderlineText from '../UnderlineText'
import Hide from 'hidden-styled'

const Preamble = () => (
  <div>
    <Box mt={[1, 6]}>
      <Flex justifyContent="center">
        <Box width={['90%', '60%']} fontWeight={300}>
          <Text fontWeight={300}>
            En tant que <strong>professionnel(le)</strong> amen(e) 
          </Text>
          <Text
            mt={3}
            lineHeight={['30px', '30px', '30px', '20px']}
            fontWeight={300}
          >
            collecter, stocker, traiter, modliser, analyser des donnes et/ou 
            concevoir des algorithmes, des produits informatiques ou des
            interfaces,
          </Text>
          <Text mt={3} fontWeight={300}>
            je suis conscient(e) de <UnderlineText text="limpact" /> que peut
            avoir mon travail sur des individus et sur la socit dans son
            ensemble.
          </Text>
        </Box>
      </Flex>
      <Box mx={-2} my={5} p={4} bg="grey_background" borderColor="grey_border">
        <Flex justifyContent="center">
          <Box width={['100%', '80%', '60%']}>
            <Description>
              Cest pourquoi je mengage  respecter les 5 principes suivants :
            </Description>
            {/*First ELEMENT*/}
            <Flex alignItems="left">
              <Relative>
                <Absolute top="5.1em">
                  <Underline />
                </Absolute>
              </Relative>
              <Number style={{ opacity: 0.07 }}>1</Number>
              <Title>Intgrit scientifique et rigueur</Title>
            </Flex>
            <Description>
              Jexploiterai les donnes avec toute la rigueur requise et en
              conformit avec les meilleurs standards de ma profession.
            </Description>

            {/*Second ELEMENT*/}
            <Flex alignItems="left">
              <Relative>
                <Absolute top="5.1em">
                  <Underline />
                </Absolute>
              </Relative>
              <Number style={{ opacity: 0.14 }}>2</Number>
              <Title>Transparence</Title>
            </Flex>
            <Description>
              Jinformerai de faon comprhensible et prcise toutes les parties
              prenantes sur les finalits, les modalits et les implications
              potentielles de mon utilisation des donnes.
            </Description>

            {/*Third ELEMENT*/}
            <Flex alignItems="left">
              <Relative>
                <Absolute top="5.1em">
                  <Underline />
                </Absolute>
              </Relative>
              <Number style={{ opacity: 0.21 }}>3</Number>
              <Title>Equit</Title>
            </Flex>
            <Description>
              Je veillerai  toujours massurer que des individus ou des groupes
              ne soient pas discrimins par rapport  des critres illgaux ou
              illgitimes, de faon directe ou indirecte, sur la base de mes
              travaux sur les donnes.
            </Description>

            {/*Fourth ELEMENT*/}
            <Flex alignItems="left">
              <Relative>
                <Absolute top="5.1em">
                  <Underline />
                </Absolute>
              </Relative>
              <Number style={{ opacity: 0.28 }}>4</Number>
              <Title>Respect</Title>
            </Flex>
            <Description>
              Jexercerai mon activit professionnelle en respectant la vie
              prive et la dignit des personnes dans toutes leurs dimensions.
            </Description>

            {/*Fifth ELEMENT*/}
            <Flex alignItems="left">
              <Relative>
                <Absolute top="5.1em">
                  <Underline />
                </Absolute>
              </Relative>
              <Number style={{ opacity: 0.35 }}>5</Number>
              <Title>Responsabilit et indpendance</Title>
            </Flex>
            <Description>
              Jassumerai mes responsabilits en cas de manquements ou de
              conflits dintrt et je donnerai lalerte si des actes illgaux
              lis  des donnes sont constats.
            </Description>
          </Box>
        </Flex>
      </Box>
    </Box>
    <Box mt={[1, 5]}>
      <Flex justifyContent="center">
        <Box width={['90%', '60%']} fontSize="16">
          <Text lineHeight="20px">
            Pour mettre en application ces principes au quotidien, Data for Good
            a rdig la <UnderlineText text="check-list thique" /> ci-dessous.
          </Text>
          <Text mt={1}>
            Cest une srie de questions quon propose de se poser {' '}
            <UnderlineText
              text="chaque
            tape"
            />{' '}
            du travail en entreprise, en recherche ou en administration.
          </Text>
        </Box>
      </Flex>
    </Box>
    <Hide xs>
      <Relative>
        <Absolute m={-2} left={0} top={-160}>
          <Image src={'/static/hand.png'} />
        </Absolute>
      </Relative>
    </Hide>
  </div>
)

export default Preamble



